@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap");

.productDataContainer {
  display: flex;
  width: 100%;
  font-family: "Poppins", sans-serif;
  margin-top: 16vh;
  flex-direction: column;
  align-items: center;
}
.rowsData {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.leftData {
  width: 40%;
  display: flex;
  flex-direction: column;
}

.descriptions-title-p {
  font-size: 35px;
  border-bottom: 1px solid rgb(224, 224, 224);
  padding-bottom: 10px;
  margin-bottom: 20px;
}

.descriptions-p {
  font-size: 18px;
  margin-bottom: 60px;
  line-height: 1.4;
  text-align: justify;
}

.ficheTechnique-p {
  font-size: 18px;
  margin-bottom: 10px;
  font-weight: 600;
}
#customers {
  border-collapse: collapse;
  font-size: 16px;
  width: 100%;
  display: block;
  height: 350px;
  overflow-y: scroll;
}
#customers tbody {
  overflow-y: auto;
}

#customers tbody td {
  border-bottom: 1px solid rgb(231, 231, 231);
  border-top: 1px solid rgb(231, 231, 231);
  padding: 10px;
}

#customers tbody tr:nth-child(even) {
  background-color: #f7f7f7;
}

.rightData {
  width: 50%;
  font-size: 20px;
  align-items: center;
  // flex-direction: column;
}

.dataImg {
  width: 100%;
  height: 100%;
}

.imgClick {
  width: 100%;
  display: flex;
  justify-content: center;
}

.imgClick img {
  width: 200px;
  height: 100px;
  margin: 10px;
  cursor: pointer;
}

.productDataVideo {
  align-content: center;
}
.productDataVideoTitle {
  border-bottom: 1px solid rgb(224, 224, 224);
}
