@import "./settings";

footer {
  display: flex;
  flex-direction: column;
  background-color: #37383c;
}

.logo-tel {
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  padding: 20px;
  background: #1b273d;
}

.logo-footer {
  display: flex;
  align-items: flex-start;
}

.footer-tel {
  display: flex;
  align-items: flex-end;
}

.fa-phone {
  color: #f20000;
  font-size: 35px;
  border: 2px solid #f20000;
  padding: 12px;
  border-radius: 5px;
}

.mail-tel {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 10px;
}

.phone-footer {
  font-size: 18px;
}

.mail-footer {
  font-size: 14px;
  color: #929292;
}

.contacts-footer {
  width: 100%;
  background-size: cover;
  background-color: #37383c;
  display: flex;
  flex-direction: row;
  justify-content: center;
  color: aliceblue;
}

.column-footer {
  margin: 20px;
  margin-right: 50px;
}

.border-column {
  background-color: #5c5c5c6f;
  width: 100%;
  height: 3px;
  margin-top: 10px;
}

.p1-footer {
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 2px;
}

.p2-footer {
  line-height: 1.6;
  font-size: 15px;
  margin-top: 15px;
}

.social-media {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #37383c;
  padding: 20px;
  border-top: 0.5px solid #5c5c5c6f;
  margin-top: 10px;
}

.social-media-content {
  display: flex;
  justify-content: space-between;
  width: 15%;
}
footer .fa-brands,
.fab {
  font-size: 35px;
}
.fa-facebook {
  color: #1b74e4;
}
.fa-twitter {
  color: #358ac4;
}
.fa-youtube {
  color: #ff0000;
}
footer .fa-instagram {
  background: radial-gradient(
    circle at 30% 107%,
    #fd5949 25%,
    #d6249f 55%,
    #285aeb 90%
  );
  -webkit-background-clip: text;
  /* Also define standard property for compatibility */
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.footer-sign-in {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  width: 30%;
}
.email-input {
  background-color: transparent;
  outline: none;
  border: none;
  font-size: 16px;
  align-content: flex-start;
  padding: 15px;
}
.submit-btn {
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 18px;
  letter-spacing: 1px;
  font-weight: bold;
  padding: 15px;
  color: #ff0000;
  align-content: flex-end;
  border-left: 1px solid rgb(174, 174, 174);
}
