.contact-container {
  display: flex;
  margin-top: 16vh;
  justify-content: center;
  width: 100%;
}

.cantact-box {
  display: flex;
  flex-direction: column;
  padding: 10px;
  background-color: white;
  border-radius: 10px;
  width: 40%;
  margin: 15px;
  align-items: center;
}

.cantacter-p {
  font-size: 25px;
  align-self: flex-start;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1px;
  margin: 10px;
  margin-bottom: 26px;
  text-align: start;
  border-bottom: #777777;
}

.form-cantact {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 20px;
}

.form-cantact div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
  flex-wrap: wrap;
}

.form-cantact div div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.form-cantact p {
  font-size: 18px;
  margin: 10px 0;
}

main input,
.message {
  padding: 10px;
  font-size: 16px;
  outline: none;
  border: 1px solid #dedede;
}

.btn-send-cantact {
  background-color: #f20000;
  outline: none;
  margin-top: 15px;
  border: none;
  padding: 15px;
  color: aliceblue;
  font-size: 18px;
  cursor: pointer;
}

.message {
  height: 230px;
}
