* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.entrepriseContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  // margin-top: 70px;
}
.entreprise {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
}
.entreprise-title {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 38px;
  text-align: center;
  line-height: 1.6;
  width: 65%;
}
.entreprise-p {
  font-size: 25px;
  text-align: center;
  line-height: 1.5;
  margin-top: 50px;
  width: 50%;
}

.bottomClickScroll {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: fixed;
  bottom: 1rem;
  padding: 10px 25px 10px 25px;
  border-radius: 20px;
  left: 50%;
  border: 2px solid coral;
  transition: all 0.3s;
  transform: translateX(-50%);
}

.bottomClickScroll:hover {
  background-color: coral;
}

.bottomClickScroll i {
  font-size: 18px;
  margin-left: 0;
  transition: all 0.5s;
  color: white;
}

.rotateBtn {
  transform: rotate(180deg);
  transition: all 0.5s;
}
