@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap");

.productContainer {
  display: flex;
  width: 90%;
  background-color: white;
  font-family: "Poppins", sans-serif;
  flex-direction: column;
  padding: 50px;
  margin: 30px;
}

.productDivTop {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 15px;
}
.productDivTop img {
  width: 150px;
  align-self: flex-end;
}

.productDivTop h1 {
  letter-spacing: 1px;
}

.productRightLeft {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.productDivLeft {
  display: flex;
  flex-direction: column;
  width: 55%;
}

.productDivRight {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 43%;
  //   margin: 20px;
  //   margin-top: 50px;
}

.productDivRight ul {
  display: flex;
  margin-bottom: 30px;
  margin-top: 40px;
}

.productDivRight ul li {
  background-color: #e9eaeb;
  color: #777777;
  font-size: 15px;
  padding: 5px 10px;
  margin-right: 10px;
}

.productDivRight p {
  font-size: 17px;
  margin-bottom: 15px;
  line-height: 1.5;
  color: #777777;
}

.productDivRight p span {
  color: rgb(119, 146, 255);
  text-decoration: underline;
  margin-left: 10px;
}
