.categoriesProduct {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-top: 8vh;
  background-color: #e9eaeb;
  justify-content: center;
  align-items: center;
}

.categoriesProductCover {
  display: flex;
  justify-content: center;
  height: 500px;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  flex-direction: column;
}

.categoriesProductCover h2 {
  color: white;
  margin-left: 20px;
  font-size: 25px;
  text-transform: uppercase;
}

.categoriesProductCover h1 {
  color: white;
  font-size: 80px;
  margin-left: 20px;
  text-transform: uppercase;
}
