@import url("https://fonts.googleapis.com/css2?family=Karla:wght@700&family=Roboto:wght@100&family=Ubuntu&display=swap");
@import "./settings";

main {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Lato", sans-serif;
  margin-top: 8vh;
}
.menu-slides {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 90px;
}

.slides {
  position: relative;
  justify-content: center;
  height: 1000px;
}

.slides-images {
  animation-name: fade;
  animation-duration: 1.5s;
  position: relative;
  justify-content: center;
}

.main-menu {
  position: absolute;
  width: 100%;
}

.menu-items {
  font-family: "Karla", sans-serif;
  display: flex;
  justify-content: center;
}

.menu-item {
  letter-spacing: 5px;
  font-size: 25px;
  cursor: pointer;
  padding: 30px 50px;
  background: #f20000;
  color: aliceblue;
  border-bottom: 5px solid transparent;
  transition: all 0.5s;
}

.menu-item:hover {
  border-bottom: 5px solid aliceblue;
}
#image1 {
  // background-image: url(../img/baner\ academii1.jpg);
  background-size: cover;
  height: 100%;
}
#image2 {
  // background-image: url(../img/baner\ Drewpol.jpg);
  background-size: cover;

  height: 100%;
}
#image3 {
  // background-image: url(../img/baner\ GPD.jpg);
  background-size: cover;
  height: 100%;
}

.prev,
.next {
  cursor: pointer;
  position: absolute;
  color: rgb(255, 255, 255);
  font-weight: bold;
  padding: 15px;
  font-size: 30px;
  transition: 0.6s ease;
  border-radius: 3px;
  user-select: none;
  background-color: rgba(93, 93, 93, 0.24);
}
.prev {
  left: 20px;
  top: 50%;
}
.next {
  right: 20px;
  top: 50%;
}

.prev:hover,
.next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

