@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap");
@import "./settings";

header {
  display: flex;
  font-family: "Poppins", sans-serif;
  flex-direction: column;
  position: absolute;
  align-items: center;
  transition: 0.3s;
  // opacity: 0.3;
  width: 100%;
  position: fixed;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: white;
}

.activateClass {
  font-weight: bold;
}

/***************************************************** NAV 1 *****************************************************/

.nav1 {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  // height: 70px;
  height: 8vh;
  background-color: #1b273d;
  align-items: center;
}

.logo {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 35px;
  color: white;

  display: flex;
  width: 15%;
  text-align: center;
  align-items: center;
  font-weight: 600;
  justify-content: center;
}

.wood {
  color: #f20000;
  margin-left: 10px;
}

.nav {
  display: flex;
  width: 30%;
}

.menuNav {
  display: flex;
  list-style: none;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.menuNav.hide {
  visibility: hidden;
}

.li-menuNav {
  display: inline-block;
  font-size: 17px;
  color: white;
  cursor: pointer;
  letter-spacing: 1px;
}

.li-menuNav:hover {
  color: rgb(214, 53, 53);
}

.cantact-header {
  display: flex;
  justify-content: center;
  width: 15%;
}

.resaux {
  display: flex;
  align-items: center;
  justify-content: center;
}

.resaux .fa-whatsapp {
  font-size: 24px;
}

.whatsApp-num {
  margin-left: 20px;
  font-size: 15px;
}
.resaux:hover {
  color: rgb(214, 53, 53);
}

/***************************************************** NAV 2 *****************************************************/
.nav2 {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 40%;
  top: 8vh;
  font-family: "Lato", sans-serif;
  position: absolute;
  height: 8vh;
  background-color: #f20000;
  border-radius: 0px 0px 5px 5px;
  transition: ease 0.2s;
}

.nav2.move {
  top: 0;
  background-color: #1b273d;
}

.nav2 ul {
  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 45%;
}

.li-nav2 {
  display: inline-block;
  font-size: 17px;
  color: white;
  cursor: pointer;
  text-align: center;
  letter-spacing: 2px;
  padding: 18px 0px;
}

.hideNavLIst {
  visibility: hidden;
}

.search-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  border-radius: 3px;
  padding: 8px;
}

.search-bar input {
  outline: none;
  background: transparent;
  border: none;
  font-size: 13px;
}

.search-bar .fa-magnifying-glass {
  margin-right: 10px;
  font-size: 14px;
}

.fa-chevron-down {
  font-size: 15px;
  font-weight: 600;
  margin-left: 5px;
}

/***************************************************** NAV 3 *****************************************************/

.nav3 {
  display: flex;
  position: absolute;
  top: 15vh;
  justify-content: space-between;
  background-color: rgb(255, 255, 255);
  border: 2px solid rgb(228, 228, 228);
  padding: 20px;

  box-shadow: 0px 0px 30px 5px rgba(0, 0, 0, 0.2);
  transition: ease-out 0.4s;
  width: 90%;
}

.nav3.nav3Top {
  top: 7vh;
}

.ul1-nav3 {
  display: flex;
  justify-content: space-around;
  align-items: baseline;
  flex-wrap: wrap;
  width: 100%;
  list-style: none;
  font-size: 16px;
}

.ul1-nav3 li {
  max-width: 180px;
}

.ul1-nav3 li p {
  cursor: pointer;
  border-bottom: 2px solid rgb(228, 228, 228);
  padding: 5px;
  font-size: 16px;
}

.ul1-nav3 li p:hover {
  border-bottom: 2px solid rgb(0, 0, 0);
}

.ul1-nav3 li a {
  display: inline;
  color: black;
}

.ul2-nav3 {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin-top: 15px;
}

.ul2-nav3 li a {
  color: rgb(135, 135, 135);
}

.ul2-nav3 li a:hover {
  color: rgb(0, 0, 0);
}

.ul2-nav3 li {
  margin-top: 16px;
  border-bottom: 1px solid rgb(228, 228, 228);
  padding-bottom: 5px;
  transition: all 0.3s;
}

.ul2-nav3 li:hover {
  border-bottom: 1px solid rgb(0, 0, 0);
}
