.marques-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  padding-top: 180px;
}

// contient deux span

.marques-row {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 80%;
  margin-top: 20px;
}

.marques-div {
  display: grid;
  justify-content: center;
  align-content: center;
}
.img-marque {
  display: grid;
  width: 350px;
  height: 200px;
  border: 1px solid rgb(222, 222, 222);
  border-radius: 10px;
  margin: 10px 10px;
}

.img-icone {
  width: 90%;
  height: 100%;
  transition: all 0.3s;
  align-items: center;
}
