.container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("../img/Entreprise/Localisation_1280x720.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 10px;
  padding: 20px;
  margin-top: 50px;
  box-shadow: 0px 0px 55px 5px rgba(0, 0, 0, 0.2);
  margin: 20px;
  width: 80%;
  height: 650px;
}
.maps-google {
  flex: 1;
  border-radius: 10px;
  border: none;
}
.maps-p {
  flex: 1;
  font-size: 25px;
  font-weight: 700;
  text-align: justify;
  margin: 0px 70px 0px 40px;
  line-height: 1.5;
  letter-spacing: 1px;
  color: black;
  align-content: center;
  align-items: center;
  backdrop-filter: blur(20px);
}
