@import url("https://fonts.googleapis.com/css2?family=Karla:wght@700&family=Roboto:wght@100&family=Ubuntu&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap");

$color-1: #1b273d;
$font-1: "Roboto:wght@100", Arial;
$font-2: "Karla:wght@700", sans-serif;
$font-3: "Cairo:wght@200", serif;
$font-4: "Ubuntu", serif;
$font-4: "Lato", sans-serif;

@mixin verticalCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#root {
  display: flex;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
  width: 100%;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  font-family: $font-4;
  // background-color: #e9eaeb;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
  cursor: pointer;
}
