.services-container {
  display: flex;
  margin-top: 16vh;
  justify-content: center;
  width: 100%;
}
.services-box {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-top: 16vh;
  background-color: white;
  border-radius: 10px;
  margin: 15px;
  width: 50%;
}

.services-box div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 50px;
  width: 600px;
}

.services-title {
  font-size: 28px;
  font-weight: 600;
  margin-bottom: 25px;
}

.services-description {
  font-size: 20px;
  text-align: justify;
}
