.carousel {
  width: 100%;
  height: 92vh;
  background-color: rgb(255, 255, 255);
  display: flex;
}

.carouselImg {
  width: 100%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  display: none;
  position: relative;
  align-items: center;
  justify-content: center;
}

.carouselImg img {
  width: 100%;
  height: 100%;
}

.carousselInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  box-shadow: 0px 0px 20px 25px rgba(0, 0, 0, 0.1);
  padding: 30px;
  backdrop-filter: blur(3px);
  position: absolute;
  color: white;
  background-color: rgba(0, 0, 0, 0.2);
  bottom: 50px;
}

.carousselInfo-designation {
  font-size: 6rem;
  color: white;
  text-align: left;
}

.carousselInfo-description1 {
  font-size: 20px;
  text-align: center;
  max-width: 500px;
  color: white;
}
.carousselInfo-description2 {
  font-size: 17px;
  text-align: justify;
  color: white;
  max-width: 500px;
  margin-top: 10px;
}

.carousselInfo button {
  border: none;
  outline: none;
  font-size: 19px;
  padding: 13px 25px 13px 25px;
  border-radius: 5px;
  align-items: center;
  display: flex;
  cursor: pointer;
  margin-top: 20px;
  background-color: transparent;
  border: 1px solid #f20000;
  transition: all 0.3s;
  color: white;
}

.carousselInfo button:hover {
  background-color: #f20000;
}

.carousselInfo button i {
  margin-left: 10px;
  font-size: 16px;
}

.activeCarousel {
  display: flex;
}

.left,
.right {
  font-size: 50px;
  color: black;
  opacity: 0.5;
  top: 50%;
  cursor: pointer;
  position: absolute;
  // transform: translateY(-50%);
  transition: all 0.3s;
}
.left {
  margin-left: 15px;
  left: 0;
}
.right {
  margin-right: 15px;
  right: 0;
}
.left:hover,
.right:hover {
  opacity: 0.8;
}

.center {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: baseline;
  position: relative;
}
.caroussel-title-video {
  display: flex;
  align-items: flex-start;
  width: 100%;
  padding: 20px;
}
.carousel-video {
  width: 600px;
  height: 300px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
